@import "../../../../styles/breakpoints";
.bd-callout {
  padding: 1.25rem;
  margin-top: 1.25rem;
  margin-bottom: 1.25rem;
  border: 1px solid #e9ecef;
  border-left-width: 0.25rem;
  border-radius: 0.25rem;
}
.bd-callout-info {
  border-left-color: #f68112;
}
.card {
  width: 100%;
  @include media("min", "xl") {
    width: 18rem;
  }
}
.fit-image {
  max-width: 200px;
  &.large {
    max-width: 800px;
  }
}
.local-uikit {
  @import "uikit/src/scss/variables-theme.scss";
  @import "uikit/src/scss/uikit.scss";
  @import "uikit/src/scss/variables.scss";
  @import "uikit/src/scss/components/_import.components.scss";
  
}
.uk-height-large.uk-background-cover {
  h1, p {
    color: white;
  }
}